.marker {
  background-color: rgb(213, 213, 213);
  border: 1px solid grey;
  border-radius: 8px;
}

.fa-xmark {
  color: white;
  position: absolute;
  top: 7px;
  right: 10px;
  padding: 4px 6px;
  border: solid 1px white;
  border-radius: 50%;
}
